.footer{
  padding: 2.5em 0;
  background-color: $gray2;
  margin-top: 3.75em;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 9em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__links{
    margin-top: .5em;
    @include grid100gap(.5em);
    color: $gray6;
    a{
      font-size: .875em;
      line-height: 125%;
    }
  }
  &__title{
    font-size: 1em;
    font-weight: 700;
    line-height: 137%;
  }
  &__c{
    font-weight: 400;
  }
}

.footer__c,
.footer-socials__title{
  font-size: .875em;
  line-height: 125%;
  color: #646464;
}

.footer-socials{
  margin: .875em 0 1.5em;
  .socials{
    margin-top: .625em;
  }
}
