.favourite{
  padding: 2.5em 0;
  .page-top__wrap{
    margin: 0 0 2em;
  }
}

.favourite-filter{
  @include grid100gap(3.75em);
  .shops-categories__items,
  .shops-categories2{
    margin: 0;
  }
}

.favourite-empty{
  display: flex;
  flex-direction: column;
  align-items: center;
  &__img{
    margin-bottom: 1.75em;
    width: 12.25em;
  }
  &__hint{
    display: flex;
    align-items: center;
    margin-top: .625em;
    img{
      width: 1.5em;
      margin-left: .25em;
    }
  }
}
