.grid,
.grid2,
.grid3,
.grid4,
.grid5,
.grid6{
  display: grid;
  gap: 1.75em;
}

.grid{
  grid-template-columns: 100%;
}

.grid2{
  grid-template-columns: repeat(2,calc(50% - .875em));
}

.grid3{
  grid-template-columns: repeat(3,calc(100% / 3 - 3.5em / 3));
}

.grid4{
  grid-template-columns: repeat(4,calc(25% - 1.3125em));
}

.grid5{
  grid-template-columns: repeat(5,calc(20% - 7em / 5));
}

.grid6{
  grid-template-columns: repeat(6,calc(100% / 6 - 8.75em / 6));
}


@media screen and (max-width: 992px){
  .grid-tablet{
    grid-template-columns: 100%;
  }

  .grid-tablet2{
    grid-template-columns: repeat(2,calc(50% - .5em));
  }
}


@media screen and (max-width: 576px){
  .grid-mob{
    grid-template-columns: 100%;
  }
}
