.main2{
  padding: .5em 0 1.875em;
}

.shops{
  padding: 1.875em 0;
  &__col{
    border-right: 1px solid $gray2;
    padding-right: .75em;
  }
  &__col2{
    grid-column: 2/-1;
  }
  &__top{
    margin-bottom: 2em;
  }
}

.shops-items{
  row-gap: 3.75em;
}

.sort{
  position: relative;
  z-index: 3;
  &._open{
    .sort__show{
      box-shadow: $shadow;
      border-color: $greenMain;
      &:after{
        transform: rotate(-180deg);
      }
    }
    .sort__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &--small{
    .sort__show{
      height: 2.5em;
    }
  }
  &__show{
    background-color: #fff;
    border: 1px solid $gray2;
    height: 3em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 .75em;
    border-radius: .25em;
    transition: all .3s;
    &:after{
      content: '';
      background-image: url(../img/select-arrow.svg);
      @include bgImgSize(100%);
      margin-left: 1em;
      width: 1.5em;
      height: 1.5em;
      transition: inherit;
    }
    &:hover{
      cursor: pointer;
      border-color: $greenMain;
    }
  }
  &__item,
  &__current{
    display: flex;
    align-items: center;
    svg{
      width: 1.5em;
      margin-right: .25em;
    }
    span{
      font-size: .875em;
      line-height: 125%;
    }
  }
  &__hidden{
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    margin-top: .5em;
    background-color: #fff;
    border-radius: .25em;
    box-shadow: $shadow;
    overflow: hidden;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
  &__top{
    padding: .75em .75em .5em;
    border-bottom: 1px solid $gray1;
  }
  &__title{
    font-weight: 700;
    font-size: .875em;
    line-height: 157%;
  }
  &__item{
    padding: 0 .75em;
    transition: all .3s;
    height: 3em;
    &:hover{
      cursor: pointer;
      background-color: $gray1;
    }
    &:after{
      content: '';
      flex-shrink: 0;
      opacity: 0;
      transition: all inherit;
      background-image: url(../img/check2.svg);
      @include bgImgSize(100%);
      margin-left: auto;
      width: 1.25em;
      height: 1.25em;
    }
    &._active{
      color: $greenMain;
      &:after{
        opacity: 1;
      }
      path{
        fill: $greenMain;
      }
    }
  }
}

.shops-categories{
  position: sticky;
  top: 9em;
  &__items{
    margin-top: 1.25em;
  }
  &__item{
    display: flex;
    align-items: center;
    border-radius: .5em;
    padding: 0 .75em;
    height: 3.25em;
    &:hover,
    &._active{
      background-color: $gray1;
      color: $greenMain;
      path:not(._stroke){
        fill: $greenMain;
      }
      ._stroke{
        stroke: $greenMain;
      }
    }
    svg{
      width: 1.5em;
      margin-right: .5em;
    }
    span{
      font-size: .875em;
      line-height: 125%;
    }
  }
}

.shop-page{
  padding: 1.5em 0 1.875em;
  .page-top__wrap{
    margin: 0 0 1.75em;
  }
}

.shop-info{
  &__top{
    display: flex;
    align-items: center;
  }
  &__img{
    margin-right: .75em;
  }
  &__city{
    margin-top: .5em;
    img{
      width: 1.25em;
      margin-right: .25em;
    }
    span{
      font-size: .875em;
    }
  }
  .shop-card__tags{
    margin-top: .75em;
  }
  .shop-card__bottom{
    padding-top: 2em;
  }
}

.avatar{
  width: 4em;
  height: 4em;
  flex-shrink: 0;
  border-radius: 50%;
  img{
    width: 100%;
    height: 100%;
    display: block;
    object-fit: cover;
    border-radius: inherit;
  }
}

.shops-categories2{
  margin-top: 3.75em;
  &__items{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 1.25em;
  }
  .shops-categories__item{
    height: 2em;
    padding: 0 .5em;
    border-radius: .25em;
    @include lastM(0 0 1em);
    &:hover,
    &._active{
      background-color: $black;
      color: $white;
      path{
        fill: $white;
      }
    }
  }
}
