.filter-slider{
  @include lastM(0 0 .75em);
  &__item{
    min-height: 1.75em;
    padding: 0 .25em;
    display: flex;
    align-items: center;
  }
  .range-slider{
    width: 100%;
    background-color: $gray2;
    border: none;
    box-shadow: none;
    height: .25em;
    border-radius: .125em;
    .noUi-connect{
      background-color: $greenLight100;
    }
    .noUi-handle{
      box-shadow: 0 .375em .75em rgba(0, 0, 0, 0.1), 0 0 .25em rgba(0, 0, 0, 0.1);
      border: none;
      background-color: $white;
      width: 1.75em;
      height: 1.75em;
      top: -.75em !important;
      right: -.75em !important;
      border-radius: 50%;
      cursor: pointer;
      &:before,
      &:after{
        display: none;
      }
      //&-upper{
      //  right: 0 !important;
      //}
    }
  }
  &__inputs{
    margin: 1em 0 .75em;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 1em;
    &-title{
      position: absolute;
      top: 50%;
      left: .5em;
      transform: translateY(-50%);
      color: $gray4;
    }
  }
  .fg{
    position: relative;
    input{
      height: 3em;
      color: $black;
      font-weight: 400;
      font-size: .875em;
      line-height: 140%;
      padding-left: 2.125em;
      padding-right: .5em;
      border-radius: .29em;
      border-color: $gray3;
    }
  }
}
