.index-page{
  background-color: $gray1;
}

.main{
  padding: 2.5em 0 1.375em;
}

.main-slider{
  overflow: hidden;
  border-radius: .75em;
  position: relative;
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
    &-pagination{
      bottom: 1em;
      width: 100%;
      @include center;
      &-bullet{
        width: 1em;
        height: 1em;
        opacity: 1;
        background-color: rgba($gray3,.5);
        margin: 0 .28125em !important;
        &-active{
          background-color: rgba($white,.8);
        }
      }
    }
  }
}

.swiper-button{
  width: 2em;
  height: 2em;
  @include center;
  svg{
    width: 100%;
  }
  &:hover{
    path{
      fill: $greenMain;
    }
  }
  &-prev{
    left: .5em;
  }
  &-next{
    right: .5em;
  }
}

.main-banner{
  position: relative;
  &__img{
    @include imgCover(24.11%);
    border-radius: .75em;
  }
  .adv-badge{
    position: absolute;
    top: .5em;
    right: .5em;
    z-index: 2;
    pointer-events: none;
  }
}

.adv-badge{
  display: flex;
  align-items: center;
  padding: 0 .25em;
  height: 1.25em;
  border-radius: .25em;
  background-color: rgba($white,.3);
  color: $gray6;
}

.collection{
  padding: 1.375em 0;
  &__inner{
    background-color: $white;
    padding: 2em 2.25em;
    border-radius: .75em;
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__grid{
    margin-top: 1.75em;
  }
  &--products{
    .collection__grid{
      row-gap: 2.75em;
    }
  }
  &--shadow{
    .collection__inner{
      box-shadow: $shadow;
    }
  }
}

.m-link{
  font-weight: 700;
  color: $black;
  padding: 0 1em;
  height: 2.5em;
  display: flex;
  align-items: center;
  background-color: $gray1;
  border-radius: .25em;
  &:hover{
    background-color: $greenMain;
    color: $white;
    path{
      fill: $white;
    }
  }
  svg{
    width: 1.5em;
    margin-left: .25em;
  }
  span{
    font-size: 1em;
  }
}

.shop-card,
.product-card{
  position: relative;
  &:hover{
    &:before{
      opacity: 1;
    }
  }
  &:before{
    content: '';
    position: absolute;
    width: calc(100% + 1.5em);
    height: calc(100% + 1.5em);
    border-radius: .5em;
    box-shadow: $shadow;
    background: #fff;
    left: -.75em;
    top: -.75em;
    transition: all .4s;
    opacity: 0;
    pointer-events: none;
  }
  &__inner{
    position: relative;
    z-index: 2;
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  &__top{
    position: relative;
  }
  &__img{
    @include imgCover(115%);
    border-radius: .75em;
  }
  .favourite-btn{
    position: absolute;
    right: .25em;
    bottom: .25em;
    z-index: 2;
  }
  &__bottom{
    margin-top: auto;
    padding-top: 1.25em;
  }
}

.product-card{
  .m-badge{
    position: absolute;
    top: .5em;
    right: .5em;
  }
  &__content{
    margin-top: 1em;
  }
  &__prices{
    display: flex;
    align-items: center;
    min-height: 1.5em;
  }
  &__price{
    font-size: 1.125em;
    line-height: 122%;
    font-weight: 800;
    color: $greenMain;
  }
  &__price2{
    font-size: .75em;
    line-height: 125%;
    text-decoration: line-through;
    color: $gray4;
    margin-left: .67em;
  }
  &__title{
    margin-top: .25em;
    font-size: 1em;
    line-height: 137%;
    @include truncate(2);
  }
  &__colors{
    margin-top: .5em;
    display: flex;
    align-items: center;
  }
}

.cart-btn{
  position: relative;
  &__view{
    @include centerAbsolute;
    display: flex;
    align-items: center;
    opacity: 0;
    white-space: nowrap;
    transition: all .3s;
    img{
      width: 1.5em;
      margin-right: .5em;
    }
  }
  ._view1{
    opacity: 1;
  }
  &:hover{
    ._view1{
      opacity: 0;
    }
    ._view2{
      opacity: 1;
    }
  }
  &._added{
    background-color: $greenMain;
    ._view1,
    ._view2{
      opacity: 0;
    }
    ._view3{
      opacity: 1;
    }
  }
}

.color-item{
  width: .5em;
  height: .5em;
  flex-shrink: 0;
  margin-right: .25em;
  border-radius: 50%;
}

.m-badge{
  display: flex;
  align-items: center;
  padding: 0 .5em;
  background-color: $red;
  height: 1.75em;
  border-radius: .25em;
  font-weight: 600;
  color: $white;
  span{
    font-size: .875em;
  }
  img{
    width: 1.25em;
    margin-right: .5em;
  }
  &--green{
    background-color: $greenMain;
  }
}


