.catalog-item{
  padding: 2.5em 0;
  &__grid{
    display: grid;
    grid-template-columns: 31.375em calc(100% - 31.375em - 18.125em - 3.5em) 18.125em;
    gap: 1.75em;
    position: relative;
  }
}

.product-gallery{
  display: grid;
  grid-template-columns: 4.875em calc(100% - 4.875em - 1.75em);
  gap: 2.5em 1.75em;
  position: relative;
  cursor: pointer;
  .gallery-item{
    &._big{
      grid-column: 2/3;
      grid-row: 1/5;
      .gallery-item__img{
        //padding-bottom: 116%;
      }
    }
  }
  .m-badge{
    position: absolute;
    top: .75em;
    right: .75em;
    z-index: 2;
  }
}

.gallery-item{
  position: relative;
  &__count{
    @include centerAbsolute;
    width: 100%;
    text-align: center;
    z-index: 2;
    color: $white;
  }
  &__img{
    min-height: 100%;
    @include imgCover(112%);
    border-radius: .25em;
    border: 1px solid $gray2;
  }
}

.gallery-modal{
  &__dialog{
    padding: 2.75em 1.25em 2.75em 2.75em;
    border-radius: 1.25em ;
    width: 77.75em;
  }
  .close-btn{
    position: absolute;
    top: 1.25em;
    right: 1.25em;
  }
}

.gallery{
  display: grid;
  grid-template-columns: 8em calc(100% - 9.25em);
  gap: 1.25em;
  align-items: center;
  &__small{
    height: 41.625em;
    padding: 2px 3.125em 2px 2px;
    position: relative;
    overflow: hidden;
    .swiper{
      overflow: unset;
      height: 100%;
      &-scrollbar{
        width: .5em;
        border-radius: .25em;
        background-color: rgba(233, 234, 234, 0.5);
        &-drag{
          border-radius: .25em;
          background-color: $gray3;
        }
      }
      &-slide{
        height: 7.125em;
        padding-bottom: 1.5em;
        &-thumb-active{
          .gallery__img:before{
            opacity: 1;
          }
        }
      }
    }
    .gallery__img{
      cursor: pointer;
      border-radius: .25em;
      background-color: $gray2;
      position: relative;
      img{
        border-radius: .25em;
      }
      &:before{
        content: '';
        @include centerAbsolute;
        width: calc(100% + 2px);
        height: calc(100% + 2px);
        border-radius: .25em;
        border: 2px solid $greenMain;
        transition: all .3s;
        opacity: 0;
        pointer-events: none;
      }
    }
  }
  &__big{
    padding: 0 6.75em;
    position: relative;
    .swiper{
      background: radial-gradient(80.21% 89.64% at 30.13% 47.34%, rgb(247, 247, 247) 0%, rgb(218, 218, 218) 100%);
      height: 31.25em;
      overflow: hidden;
      border-radius: .25em;
      &-slide{
        height: 100%;
        padding: 1.625em;
      }
    }
  }
  &__img{
    height: 100%;
    width: 100%;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
  .swiper-button{
    background-color: $gray1;
    border-radius: 50%;
    width: 2.75em;
    height: 2.75em;
    transition: all .3s;
    svg{
      width: 1.5em;
    }
    path{
      fill: $black;
    }
    &:hover{
      background-color: $greenMain;
      path{
        fill: $white;
      }
    }
  }
}

.catalog-info{
  @include grid100gap(1.75em);
  &__text{
    @include grid100gap(.625em);
  }
  &__nav{
    display: flex;
  }
  &__link{
    font-size: 1em;
    line-height: 137%;
    font-weight: 700;
    color: $greenMain;
    border-bottom: 1px solid $greenMain;
    &:hover{
      color: $greenDark;
      border-color: $greenDark;
    }
  }
}

.color{
  &__items{
    margin-top: 1em;
    display: grid;
    grid-template-columns: repeat(5,calc(20% - 6em / 5));
    gap: 1.5em;
  }
}

.color-link{
  position: relative;
  padding-bottom: 100%;
  &._active{
    .color-link__inner{
      border-color: $greenMain;
    }
  }
  &:hover{
    .color-link__hint{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__inner{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: .25em;
    border: 1px solid $gray2;
    @include center;
  }
  &__round{
    width: 60%;
    height: 60%;
    display: block;
    border-radius: 50%;
  }
  &__hint{
    opacity: 0;
    transition: all .3s;
    pointer-events: none;
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    margin-top: -1px;
    background-color: $gray1;
    border-radius: .25em;
    padding: .25em 1em;
    @include center;
    min-height: 2.625em;
    &:before{
      content: '';
      position: absolute;
      width: 1.375em;
      height: .75em;
      background-image: url(../img/beak.svg);
      @include bgImgSize(100%);
      top: -.5em;
      left: 50%;
      transform: translateX(-50%);
    }
  }
}

.size{
  &__items{
    display: flex;
    margin-top: .5em;
    width: calc(100% + 1.25em);
  }
}

.size-item{
  margin: .5em 1.25em 0 0;
  padding: 0 .9375em;
  height: 2.5em;
  display: flex;
  align-items: center;
  border: 1px solid $gray2;
  border-radius: .25em;
  cursor: pointer;
  &:hover,
  &._active{
    border-color: $greenMain;
    color: $black;
  }
}

.props{
  @include grid100gap(.75em);
}

.prop-item{
  display: flex;
  justify-content: space-between;
  &__title,
  &__desc{
    span{
      font-size: .875em;
      line-height: 125%;
    }
  }
  &__title{
    color: $gray5;
  }
  &__desc{
    order: 2;
  }
  &:after{
    content: '';
    flex-grow: 1;
    height: 1px;
    min-width: .5em;
    background-color: $gray5;
    margin: 0 .5em;
    align-self: flex-end;
    margin-bottom: .25em;
  }
}
