.header{
  position: sticky;
  top: 0;
  z-index: 20;
  background-color: $white;
  transition: all .3s;
  &__inner{
    display: flex;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 5.75em;
    margin-right: 1.75em;
    img{
      width: 100%;
      display: block;
    }
  }
  .sort__show:after{
    margin-left: .25em;
  }
  &._fix{
    transform: translateY(-3.25em);
    box-shadow: $shadow;
    .header-category{
      flex-direction: row;
      width: auto;
      padding: 0 .5em;
      height: 2em;
      svg{
        margin: 0 .25em 0;
      }
      br{
        display: none;
      }
    }
  }
  &._open-catalog{
    .header-category{
      box-shadow: unset;
    }
    .catalog-menu{
      opacity: 1;
      pointer-events: unset;
    }
    .catalog-btn{
      ._view2{
        opacity: 1;
      }
      ._view1{
        opacity: 0;
      }
    }
  }
  &--catalog-item{
    &._fix{
      .header-bottom{
        display: none;
      }
      .header-product{
        display: flex;
      }
    }
  }
}

.header-top{
  padding: .5em 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__btn{
    padding: 0 .625em;
    height: 2.5em;
    margin-left: 1.75em;
    &:hover{
      color: $black;
      background-color: $greenLight50;
    }
    &._type2{
      background-color: $greenLight25;
      padding: 0 1em;
    }
    img{
      margin-right: .25em;
    }
    span{
      font-size: .875em;
    }
  }
}

.header-city{
  position: relative;
  &__show{
    display: flex;
    align-items: center;
    font-weight: 700;
    color: $greenMain;
    img{
      width: 1.25em;
      margin-right: .25em;
    }
    span{
      font-size: .875em;
    }
  }
}

.city-ask{
  position: absolute;
  top: 100%;
  left: 0;
  margin-top: 1em;
  width: 24.75em;
  background: rgba(26, 30, 30, 0.92);
  border-radius: .75em;
  padding: 1.25em;
  color: $white;
  z-index: 5;
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  &._open{
    opacity: 1;
    pointer-events: unset;
  }
  &:before{
    content: '';
    position: absolute;
    bottom: 100%;
    left: 2em;
    width: 1.5em;
    height: .75em;
    background-image: url(../img/triangle.svg);
    @include bgImgSize(100%);
  }
  &__desc{
    margin-top: .75em;
  }
  &__nav{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
    margin-top: 1em;
  }
}

.header-middle{
  display: flex;
  align-items: center;
  padding: .625em 0;
  transition: all .3s;
}

.catalog-btn{
  margin: 0 1.25em 0 1.75em;
  span{
    font-size: .875em;
  }
  .catalog-btn__icon{
    font-size: 1em;
  }
  &__icon{
    width: 1.5em;
    position: relative;
    margin-right: .5em;
    img{
      @include centerAbsolute;
      width: 100%;
      margin: 0;
    }
    ._view2{
      opacity: 0;
      width: 65%;
    }
  }
}

.header-bottom{
  padding: .75em 0 1.5em;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
}

.header-category{
  width: calc(12.5% - 1.25em * 7 / 8);
  padding: .75em;
  @include center;
  flex-direction: column;
  border-radius: .25em;
  background-color: $white;
  box-shadow: $shadow;
  text-align: center;
  &:hover{
    color: $greenMain;
    path{
      fill: $greenMain;
    }
  }
  svg{
    height: 1.5em;
    flex-shrink: 0;
    margin-bottom: .25em;
  }
  span{
    font-size: .875em;
    line-height: 125%;
  }
}

.burger{
  display: none;
}
