.filter{
  @include grid100gap(1.5em);
}

.catalog-category{
  &__items{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: .75em;
  }
  &__back,
  &__item{
    @include lastM(0 0 .25em);
    display: flex;
    align-items: center;
    border-radius: .25em;
    span{
      font-size: .875em;
    }
  }
  &__item{
    padding: 0 .5em;
    height: 1.625em;
    &:hover,
    &._active{
      background-color: $black;
      color: $white;
    }
  }
  &__back{
    height: 1.75em;
    svg{
      width: 1.25em;
      margin-right: .25em;
    }
    &:hover{
      color: $greenMain;
      path{
        fill: $greenMain;
      }
    }
  }
}

.filter-region{
  display: flex;
  align-items: center;
  .m-switch{
    margin-left: 1em;
  }
}

.filter-checks{
  margin-top: .75em;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &:not(._open){
    .m-check:nth-child(5)~.m-check{
      display: none;
    }
    &._less{
      .m-check:nth-child(3)~.m-check{
        display: none;
      }
    }
  }
  &._open{
    .filter-checks__more{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
  .m-check{
    margin-bottom: .25em;
    &:last-child{
      margin-bottom: 0;
    }
  }
  &__more{
    margin-top: .25em;
    display: flex;
    align-items: center;
    color: $greenMain;
    font-weight: 700;
    &:hover{
      color: $greenDark;
    }
    span{
      font-size: 1em;
      line-height: 138%;
    }
    svg{
      width: 1.5em;
      margin-left: .25em;
    }
    ._view2{
      display: none;
    }
  }
}

.color-check{
  i{
    width: 1.25em;
    height: 1.25em;
    display: block;
    border-radius: 50%;
    margin-right: .25em;
    border: 1px solid $gray2;
  }
}

.filter-result{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
