.header-search{
  flex-grow: 1;
  position: relative;
  display: flex;
  border: 1px solid $black;
  border-radius: .25em;
  height: 3em;
  &._open-results{
    .search-result,
    .header-search__clear{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__field{
    flex-grow: 1;
    height: 100%;
    position: relative;
    input{
      height: 100%;
      width: 100%;
      font-size: .875em;
      font-family: $font1;
      padding-left: 1.14em;
      padding-right: 1.14em;
      border-radius: .28em;
      border: none;
      color: $black;
      &::placeholder{
        color: $gray6;
      }
    }
  }
  &__clear{
    position: absolute;
    right: 1em;
    top: 50%;
    transform: translateY(-50%);
    width: 1.5em;
    @include center;
    opacity: 0;
    pointer-events: none;
    svg{
      width: 100%;
    }
    &:hover{
      path{
        fill: $red;
      }
    }
  }
  &__btn{
    width: 4.75em;
    padding: 0;
    margin-right: -1px;
    margin-top: -1px;
  }
}

.search-categories{
  height: 100%;
  &._selected{
    .search-categories__show{
      display: none;
    }
    .search-categories__selected{
      display: flex;
    }
  }
  &._open{
    .search-categories__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__selected,
  &__show{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 0 .75em;
    background-color: $gray1;
    border-radius: .25em 0 0 .25em;
    transition: all .3s;
    color: $gray6;
    white-space: nowrap;
    &:hover{
      color: $greenMain;
      cursor: pointer;
      path{
        fill: $greenMain;
      }
    }
    svg{
      width: 1.5em;
      margin-left: .5em;
    }
  }
  &__current{
    font-size: .875em;
    line-height: 125%;
  }
  &__selected{
    display: none;
    background-color: $greenMain;
    color: $white;
    &:hover{
      color: $white;
    }
  }
  &__clear{
    width: 1.5em;
    @include center;
    margin-left: .5em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__hidden{
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    z-index: 5;
    position: absolute;
    top: 100%;
    left: 0;
    padding: .25em;
    margin-top: .5em;
    background-color: $white;
    box-shadow: $shadow;
  }
  .shops-categories__item{
    white-space: nowrap;
    transition: all .3s;
    cursor: pointer;
  }
}

.search-result{
  opacity: 0;
  pointer-events: none;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  margin-top: .5em;
  padding: .5em 0 0;
  border: 1px solid $gray1;
  border-radius: .25em;
  background-color: $white;
  z-index: 10;
  overflow: hidden;
  &__link{
    display: flex;
    align-items: center;
    padding: .75em;
    &:hover{
      color: $black;
      background-color: $gray1;
    }
    img{
      width: 1.5em;
      margin-right: .5em;
    }
    span{
      font-size: .875em;
      line-height: 125%;
    }
  }
}

.header-action{
  display: flex;
  &__btn{
    margin-left: 1.75em;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    min-width: 4.875em;
    img{
      height: 1.75em;
      margin-bottom: .25em;
    }
    &:hover{
      color: $black;
      opacity: .8;
    }
    .pin{
      top: -.25em;
      left: 50%;
      margin-left: .6875em;
      position: absolute;
    }
  }
  &__title{
    font-size: .875em;
    line-height: 125%;
    font-weight: 700;
  }
}

.pin{
  display: flex;
  align-items: center;
  padding: 0 .5em;
  height: 1.625em;
  border-radius: .75em;
  background-color: $greenMain;
  font-weight: 600;
  color: $white;
  span{
    font-size: .875em;
  }
}
