.catalog-cart{
  padding: 1.25em 1.25em 1.5em;
  border-radius: .5em;
  background-color: $white;
  box-shadow: $shadow;
  &__prices{
    display: flex;
    align-items: flex-end;
  }
  &__price{
    font-size: 2em;
    line-height: 130%;
    letter-spacing: -0.01em;
    font-weight: 800;
    color: $greenMain;
  }
  &__price2{
    padding: 0 0 .5em .75em;
    font-weight: 600;
    font-size: 1em;
    line-height: 130%;
    text-decoration: line-through;
    color: $gray4;
  }
  &__items{
    margin-top: 1em;
    @include grid100gap(.5em);
  }
  &__item{
    display: flex;
    align-items: center;
    color: $gray5;
    img{
      width: 1.5em;
      margin-right: .25em;
    }
    span{
      font-size: .875em;
      line-height: 125%;
    }
  }
  &__nav{
    margin-top: 1.75em;
    display: grid;
    grid-template-columns: calc(100% - 5.25em) 4em;
    gap: 1.25em;
    .cart-btn{
      &:not(._added):hover{
        ._view1{
          opacity: 1;
        }
      }
    }
    .favourite-btn{
      width: 100%;
      height: 3em;
      @include center;
      border: 1px solid $gray2;
      border-radius: .25em;
      &:hover{
        border-color: $greenMain;
      }
      svg{
        width: 1.8125em;
      }
    }
  }
}

.catalog-item{
  .shop-info{
    margin-top: 2.75em;
    .shop-card__bottom{
      gap: .75em;
    }
  }
  &__share{
    position: absolute;
    right: 0;
    bottom: 100%;
    margin-bottom: 2.25em;
    padding: 0 .5em;
    height: 2em;
    svg{
      margin-right: .25em;
    }
    span{
      font-size: .875em;
    }
    &:hover{
      color: $greenMain;
      path{
        fill: $greenMain;
      }
    }
  }
}

.products{
  padding: 2.5em 0;
  &__grid{
    margin-top: 1.75em;
  }
}

.catalog-detail{
  padding: 2.5em 0;
  &__inner{
    @include grid100gap(3.75em);
    max-width: 64.5em;
  }
}

.text{
  font-size: 1em;
  line-height: 137%;
  p{
    line-height: inherit;
    @include lastM(0 0 1em);
  }
}

.catalog-desc{
  .text{
    margin-top: 1em;
  }
}

.catalog-set{
  margin-top: 1.25em;
  .text{
    margin-top: .75em;
  }
}

.catalog-props{
  margin-top: 1.25em;
  @include grid100gap(2.375em);
}


.props-group{
  &__items{
    margin-top: .75em;
    row-gap: 1em;
    &:first-child{
      margin-top: 0;
    }
  }
  .prop-item{
    &__desc{
      width: 17.125em;
      flex-shrink: 0;
    }
  }
}
