@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2em;
    height: 3em;
    cursor: pointer;
    font-weight: 700;
    border-radius: .25em;
    &:focus {
        box-shadow: none;
    }
    img,
    svg{
        width: 1.5em;
        margin-right: .5em;
    }
    span{
        font-size: 1em;
    }
    &--small{
        height: 2.375em;
        padding: 0 1em;
    }
}

.m-btn-black{
    background-color: $black;
    color: $white;
    &:hover{
        background-color: $greenMain;
        color: $white;
    }
}

.m-btn-green{
    background-color: $greenLight100;
    color: $white;
    &:hover{
        background-color: $greenMain;
        color: $white;
    }
}

.m-btn-white{
    background-color: $white;
    color: $black;
    &:hover{
        background-color: $greenMain;
        color: $white;
    }
}

.m-btn-gray-outline{
    color: $black;
    border: 1px solid $gray2;
    &:hover{
        border-color: $greenMain;
        background-color: $greenMain;
        color: $white;
        path{
            fill: $white;
        }
    }
}

.m-btn-gray{
    background-color: $gray1;
    color: $black;
    &:hover{
        background-color: $greenMain;
        color: $white;
        path{
            fill: $white;
        }
    }
}
