._flex{
  display: flex;
  align-items: center;
}

.shop-card{
  &__img{
    padding-bottom: 89.9%;
  }
  &__content{
    margin-top: 1em;
  }
  &__city{
    margin-top: .25em;
    img{
      width: 1.5em;
      margin-right: .25em;
    }
  }
  &__tags{
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + .5em);
    .m-tag{
      margin: .5em .375em 0 0;
    }
  }
  &__bottom{
    @include grid100gap(1em);
  }
}

.m-tag{
  display: flex;
  align-items: center;
  padding: 0 .45em;
  height: 1.5em;
  background-color: $gray1;
  border-radius: .5em;
  span{
    font-size: .75em;
  }
}

.banner-card{
  padding: 0 1em;
  @include center;
  height: 14.75em;
  background: linear-gradient(180deg, #008e7d 0%, #52c5a9 100%);
  border-radius: .5em;
  text-align: center;
  position: relative;
  &__title{
    font-size: 2.75em;
    letter-spacing: -0.03em;
    color: #f5edea;
    font-weight: 700;
  }
  &__img{
    height: 100%;
    display: block;
  }
  .adv-badge{
    position: absolute;
    top: .5em;
    right: .5em;
    color: #646464;
  }
}
