@import "../vars.scss";
.page-top{
    padding: 1.5em 0;
    &__wrap{
        margin-top: 1em;
        display: flex;
        align-items: flex-end;
    }
    &__desc{
        padding: 0 0 .25em .625em;
    }
}

.breadcrumbs {
    &__item {
        font-size: .875em;
        line-height: 125%;
        position: relative;
        display: inline;
        color: $greenMain;
        &:not(:first-child) {
            margin-left: 2.2em;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                width: 1.7em;
                height: 1.7em;
                top: -.1em;
                right: 100%;
                margin-right: .25em;
                background-image: url(../img/bread-arrow.svg);
                @include bgImgSize(100%);
            }
        }
    }
    a{
        &:hover{
            color: $greenDark;
        }
    }
}
