.catalog-menu{
  position: absolute;
  top: 8.5em;
  left: 0;
  width: 100%;
  padding: 1.25em;
  background-color: #fff;
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  box-shadow:  0 .5em .25em 0 rgba(0, 0, 0, 0.08);
  &__inner{
    position: relative;
  }
  &__items{
    width: 18.125em;
    padding-right: .75em;
    border-right: 1px solid $gray2;
  }
}

.menu-item{
  &._active{
    .menu-item__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &._active .menu-item__show,
  &__show:hover{
    background-color: $gray1;
    color: $greenMain;
    path:not(._stroke){
      fill: $greenMain;
    }
    ._stroke{
      stroke: $greenMain;
    }
  }
  &__show{
    display: flex;
    align-items: center;
    cursor: pointer;
    padding: 0 .75em;
    border-radius: .5em;
    transition: all .3s;
    height: 3.875em;
    &:hover{

    }
    svg{
      width: 1.5em;
      margin-right: .5em;
    }
    span{
      font-size: .75em;
      line-height: 125%;
    }
  }
  &__hidden{
    position: absolute;
    top: 0;
    right: 0;
    width: calc(100% - 18.125em);
    padding-left: 1.75em;
    opacity: 0;
    pointer-events: none;
  }
}

.submenu{
  .page-top__wrap{
    margin: 0 0 2.25em;
  }
  &__items{
    width: 41em;
    max-width: 100%;
    column-count: 2;
    column-gap: 1.75em;
    li{
      margin-bottom: 1.75em;
      a{
        font-size: 1em;
        line-height: 137%;
        font-weight: 700;
      }
      ul{
        margin-top: 1.25em;
        padding-left: .5em;
        li{
          margin-bottom: 1.375em;
          &:last-child{
            margin-bottom: 0;
          }
          a{
            font-weight: 500;
            color: $gray5;
            &:hover{
              color: $greenMain;
            }
          }
        }
      }
    }
  }
}

.header-product{
  padding: .75em 0 1em;
  display: none;
  align-items: center;
  justify-content: space-between;
  &__info{
    display: flex;
    align-items: center;
  }
  &__img{
    width: 4.875em;
    height: 5.625em;
    margin-right: 1.75em;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
      border-radius: .25em;
    }
  }
  &__text{
    @include grid100gap(.5em);
  }
  &__price{
    color: $greenMain;
  }
  &__wrap{
    flex-grow: 1;
    max-width: 46.375em;
    border-left: 1px solid $gray3;
    min-height: 5.75em;
    margin-left: 1.75em;
    padding-left: 1.75em;
    display: flex;
    align-items: center;
  }
  .shop-info{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }
  .catalog-cart__nav{
    width: 18.125em;
    margin: 0 0 0 1em;
  }
}
